@mixin flex-box {
  display: flex;
  justify-content: center;
  align-items: center;
}
@mixin flex-row {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
@mixin flex-column {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
